<template>
	<div id="goods">
		<div class="title">
			{{goods!=""?goods.subject_collection.name:""}}
			<router-link to="/movies">更多</router-link>	
		</div>
		<div class="goodsBox">
			<ul>
				<router-link tag="li" to="/movies" :key="index" v-for="(v,index) in goods.subject_collection_items">
					<div class="imgBox">
						<img :src="v.cover.url">
					</div>
					<div class="name">{{v.title}}</div>
					<div class="stars">
						<span class="star" v-if="v.rating">
							<i v-for="(v1,index1) in 5">
								<img v-show="v.rating.value/v.rating.max>=(index1+1)/5" src="@/assets/img/star.png">
								<img v-show="v.rating.value/v.rating.max<(index1+1)/5" src="@/assets/img/starbg.png">
							</i>
						</span>
						<span class="fen" v-if="v.rating">{{v.rating.value}}</span>
						<span class="null" v-if="!v.rating">{{v.null_rating_reason}}</span>
					</div>
				</router-link>
			</ul>
		</div>
	</div>
</template>	

<script type="text/javascript">
	export default {
		props:["goodsArr"],
		data(){
			return {
				goods:""
			}
		},
		methods:{

		},
		created:function(){
			
		},
		watch:{
			goodsArr(n,o){
				this.goods = n;
			}
		}
	}
</script>

<style lang="less" scoped>
	#goods{
		padding-top:20px;
		.title{
			padding:0 26px;
			height:48px;
			font-size:34px;
			line-height:48px;
			a{
				float:right;
				font-size:28px;
				color:#42bd56;
			}
		}
		.goodsBox{
			padding:28px 0;
			ul{
				white-space: nowrap;
				overflow-y:hidden;
				overflow-x:auto;
				-webkit-overflow-scrolling: touch;
				&::-webkit-scrollbar{
			        background-color:transparent;
			    }
				li{
					display: inline-block;
					height:284px;
					width:200px;
					background:#ccc;
					margin-left:18px;
					text-align: center;
					.imgBox{
						width:100%;
						height:284px;
						overflow:hidden;
						img{
							width:100%;
							display: block;
						}
					}
					.name{
						display: block;
						margin-top:20px;
						font-size:30px;
					}
					.stars{
						margin-top:10px;
						.star{
							i{
								width:20px;
								height:20px;
								display: inline-block;
								img{
									display: block;
									width:100%;
									height:100%;
								}
							}
						}
						.fen,.null{
							color:#aaa;
						}
					}
					&:first-child{
						margin-left:36px;
					}
					// &:last-child{
					// 	margin-right:36px;
					// }
				}
			}
		}
	}
</style>


