<template>
  <div id="page">
  	<ul>
  		<router-link :key="v.id" tag="li" :to="'list/'+v.id" v-for="(v,index) in lists">{{v.name}}</router-link>
  	</ul>
  	{{msg}}
  </div>
</template>

<script type="text/javascript">
  
 export default{
 	data(){
 		return {
 			msg:"",
 			lists:[
 				{name:"新闻",id:1},
 				{name:"军事",id:2},
 				{name:"游戏",id:3},
 				{name:"政治",id:4},
 			],
 		}
 	},
 	methods:{

 	},
 	created(){
 		this.msg = this.$store.state.count;
 	}
 }

</script>

<style type="text/css" scoped>
  
</style>


