<template>
	<div id="navBox">
		<router-link id="log" to="/movise">豆瓣</router-link>
		<div class="nav">
			<ul>
			  	<li :key="v.name" v-for="(v,index) in nav">
			  		<router-link :to="v.url" :class="v.class">{{v.name}}</router-link>
			  	</li>
		    </ul>
			<!-- <span class="search">
				<i></i>
				<i></i>
			</span> -->
		</div>
	    
	</div>
</template>

<script type="text/javascript">
  export default{
  	data(){
  		return {
  			nav:[
  				{name:"电影",url:"/movies",class:"movies"},
  				{name:"图书",url:"/books",class:"books"},
  				{name:"广播",url:"/airing",class:"airing"},
  				{name:"小组",url:"/groups",class:"groups"}
  			],
  		}
  	}
  }
</script>

<style lang="less" type="text/css" scoped>
	.movies{
		color:#2384E8;
	}
	.books{
		color:#9F7860;
	}
	.airing{
		color:#E4A813;
	}
	.groups{
		color:#2AB8CC;
	}
	.search{
		height:36px;
		width:50px;
	}
	#navBox{
		width:100%;
		color:#666;
		overflow:hidden;
		padding:0 36px;
		border-bottom:1px solid #dedede;
		#log{
			height:88px;
			line-height: 88px;
			font-weight: bold;
			color:#42bd56;
			font-size:42px;
		}
		.nav{
			float:right;
			ul{
				overflow: hidden;
				display:flex;
				line-height: 88px;
				li{
					flex:1;
					text-align: center;
					height:88px;
					line-height: 88px;
					margin-right:38px;
					&:last-child{
						border-right:none;
					}
					&.router-link-active{
						color:blue;
					}
					a{
						font-size:28px;
					}
				}
			}
		}
		
	}
</style>


