<template>
  <div id="page">
  	<div class="title">列表页</div>
  	{{text}}
  </div>
</template>

<script type="text/javascript">
  
 export default{
 	data(){
 		return {
 			listId:0,
 			msg:"hello world",
 			msgBox:[
 				{id:0,msg:"该页面为空"},
 				{id:1,msg:"新闻"},
 				{id:2,msg:"军事"},
 				{id:3,msg:"游戏"},
 				{id:4,msg:"政治"},
 			],
 			text:"",
 		}
 	},
 	methods:{

 	},
 	created(){

 		this.listId = this.$route.params.id;
 		for(var i = 0; i < this.msgBox.length; i++){
 			if(this.listId == this.msgBox[i].id){
 				this.text  = this.msgBox[i].msg;
 			}
 		}
 		console.log(this.text)
 	}
 }

</script>

<style type="text/css" scoped>
  .title{
	width:100%;
	height:44px;
	background:#dedede;
	color:#333;
	font-size:18px;
	text-align: center;
	line-height: 44px;
  }
</style>


