<template>
  <div id="page">
	<goodsList :key="index" v-for="(v,index) in goodsBox" :goodsArr="v"></goodsList>
  </div>
</template>

<script type="text/javascript">
  
  import { jsonp } from "@/assets/js/jsonp.js";
  import goodsList from "@/components/goodsList";

 export default{

 	data(){
 		return {
 			msg:"",
 			goodsBox:{
 				0:[],
 				1:[],
 				2:[]
 			},
 		}
 	},
 	methods:{
 		getList(){

 			
 			var url0 = "https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items?os=ios&for_mobile=1&callback=jsonp0&start=0&count=8&loc_id=108288&_=0";

 			var url1 = "https://m.douban.com/rexxar/api/v2/subject_collection/movie_free_stream/items?os=ios&callback=jsonp1&start=0&count=8&loc_id=108288&_=0";

 			var url2 = "https://m.douban.com/rexxar/api/v2/subject_collection/movie_latest/items?os=ios&callback=jsonp2&start=0&count=8&loc_id=108288&_=0";
 			
 			jsonp(url0,"jsonp0",function(data){
 				this.goodsBox[0] = data;
 			}.bind(this));
 			jsonp(url1,"jsonp1",function(data){
 				this.goodsBox[1] = data;
 			}.bind(this));
 			jsonp(url2,"jsonp2",function(data){
 				this.goodsBox[2] = data;
 			}.bind(this));

 			console.log(this.goodsBox)

 		}
 	},
 	created(){

 		this.getList();

 	},
 	components:{
 		goodsList
 	}
 }

</script>

<style type="text/css" scoped>
  
</style>


